import React, { HTMLProps } from 'react'
import { Container, Row, Col, Navbar, Button, Alert, ListGroup, Dropdown, ButtonGroup, Modal, Form } from 'react-bootstrap'
import Canvas from './Canvas'

import AceEditor from 'react-ace'
import 'ace-builds/src-noconflict/theme-github'

import { Program, ValueHandle, Failure } from 'dms'

type EventTrigger = { keyDown: string } | { keyUp: string } | { timer: number }
type EventTriggers = { [event: string]: EventTrigger }

function setTrigger(event: string, trigger: EventTrigger): (et: EventTriggers) => EventTriggers {
  return et => ({ ...et, [event]: trigger })
}

function removeTrigger(event: string): (et: EventTriggers) => EventTriggers {
  return et => {
    const { [event]: _, ...rest } = et
    return rest
  }
}

export default function App() {
  const [code, setCode] = React.useState(exampleCode())
  const [program, setProgram] = React.useState<Program | null>(null)
  const [events, setEvents] = React.useState<string[]>([])
  const [error, setError] = React.useState<Failure | string | null>(null)
  const [triggers, setTriggers] = React.useState<EventTriggers>({})

  function failure(err: unknown) {
    if (err instanceof Failure) {
      setError(err)
    } else {
      setError(String(err))
    }
  }

  const triggerEvent = React.useMemo(() => {
    if (!program) return {}
    const fs: { [event: string]: () => void } = {}
    for (let i = 0; i < program.num_base_events(); i++) {
      const event = program.base_event(i)
      fs[event] = () => {
        if (program) {
          try {
            program.trigger(event)
            setEvents([...events, event])
          } catch (err) {
            setProgram(null)
            failure(err)
          }
        }
      }
    }
    return fs
  }, [program, events])

  const onTrigger = React.useMemo(() => {
    return (event: string) => triggerEvent[event]()
  }, [triggerEvent])

  function run() {
    try {
      const newProgram = Program.new(code)
      setProgram(newProgram)
      setEvents([])
      setError(null)

      const newEvents = new Set<string>()
      for (let i = 0; i < newProgram.num_base_events(); i++) {
        newEvents.add(newProgram.base_event(i))
      }
      const newTriggers: EventTriggers = {}
      for (const event in triggers) {
        if (newEvents.has(event)) {
          newTriggers[event] = { ...triggers[event] }
        }
      }
      setTriggers(newTriggers)
    } catch (err) {
      failure(err)
    }
  }

  function baseEvents() {
    if (!program) return []
    const events: string[] = []
    for (let i = 0; i < program.num_base_events(); i++) {
      events.push(program.base_event(i))
    }
    return events
  }

  function globals() {
    if (!program) return []
    const globs = program.globals()
    const handles: ValueHandle[] = []
    for (let i = 0; i < globs.len(); i++) {
      handles.push(globs.get(i))
    }
    return handles
  }

  return (
    <Container fluid className={'vh-100 d-flex flex-column'}>
      <Row>
        <Navbar variant={'dark'} bg={'dark'}>
          <Navbar.Brand>
            Dynamic Modal Playground
          </Navbar.Brand>
          <Button variant={'success'} onClick={run}>▶ Run</Button>
        </Navbar>
      </Row>

      <Row className={'justify-content-center'} style={{ height: '100%' }}>
        <Col md={{ span: 6 }} style={{ height: '100%' }}>
          <div style={{ height: '100%' }}>
            <AceEditor
              value={code}
              markers={error instanceof Failure
                ? [{
                  startRow: lineAndColumn(code, error.start).line-1,
                  startCol: lineAndColumn(code, error.start).column-1,
                  endRow: lineAndColumn(code, error.end).line-1,
                  endCol: lineAndColumn(code, error.end).column-1,
                  className: 'syntax-error-marker',
                  type: 'text',
                }]
                : []}
              highlightActiveLine={error === null}
              scrollMargin={[0, 500]}
              onChange={newCode => {
                setCode(newCode);
                setError(null);
              }}
              theme={'github'}
              fontSize={14}
              tabSize={2}
              width={'100%'}
              height={'100%'}
              name={'DYNAMIC_MODAL_EDITOR'}
              editorProps={{ $blockScrolling: true }}
            />
          </div>
        </Col>

        <Col md={{ span: 6}}>
          {program && (
            <>
              <Row className={'p-2'}>
                {baseEvents().map(baseEvent => (
                  <Col key={baseEvent} xs={'auto'} className={'p-1'}>
                    <EventButton
                      event={baseEvent}
                      lastEvent={events[events.length - 1]}
                      onTrigger={() => onTrigger(baseEvent)}
                      {...{ triggers, setTriggers }}
                    />
                  </Col>
                ))}
              </Row>
              <Row>
                <Col>
                  <TriggerArea {...{ triggers, onTrigger }} />
                </Col>
              </Row>
              <Row className={'mb-2'}>
                <Col>
                  <ListGroup style={{ maxHeight: '66vh', overflowY: 'scroll' }}>
                    {globals().map((glob, i) => {
                      const name = glob.is_dynamic() ? `(@ ${glob.name()})` : glob.name()
                      let value: string
                      let pixels: BigInt64Array | undefined
                      try {
                        if (glob.is_dynamic()) {
                          const current = program?.current_value_of_dynamic(glob)
                          value = current?.show() ?? '(inactive)'
                          pixels = current?.pixels()
                        } else {
                          value = glob.show()
                          pixels = glob.pixels()
                        }
                      } catch (err) {
                        value = '(error)'
                        failure(err)
                      }
                      return (
                        <ListGroup.Item key={i} variant={'info'}>
                          <Row className={'align-items-center'}>
                            <Col xs={{ span: 4 }}><code className={'text-dark m-0'}>{name}</code></Col>
                            {!pixels && <Col><code className={'text-dark m-0'}>{value}</code></Col>}
                            {pixels && <Col><Pixels width={'300'} height={'300'} pixels={pixels} /></Col>}
                          </Row>
                        </ListGroup.Item>
                      )
                    })}
                  </ListGroup>
                </Col>
              </Row>
            </>
          )}
        </Col>
      </Row>

      {error && (
        <Row>
          <Col>
            <Alert variant={'danger'}>
              {typeof error === 'string'
                ? error
                : error.message()}
            </Alert>
          </Col>
        </Row>
      )}
    </Container>
  )
}

function EventButton(props: {
  event: string,
  lastEvent?: string,
  onTrigger: () => void,
  triggers: EventTriggers,
  setTriggers: React.Dispatch<React.SetStateAction<EventTriggers>>,
}): JSX.Element {
  const { event, lastEvent, onTrigger, triggers, setTriggers } = props
  const variant = event === lastEvent ? 'primary' : 'dark'

  const [bindingMode, setBindingMode] = React.useState<null | 'key down' | 'key up' | 'timer'>(null)

  function BindingModal(): JSX.Element {
    const [key, setKey] = React.useState<string | null>(null)
    const [timer, setTimer] = React.useState<number>(1000)
    const [focused, setFocused] = React.useState(false)

    return (
      <Modal show={bindingMode !== null} onHide={() => setBindingMode(null)}>
        <Modal.Header>
          <Modal.Title>
            Bind <strong>{event}</strong> to a {bindingMode}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {(bindingMode === 'key down' || bindingMode === 'key up') && (
            <Alert
              className={'mb-0'}
              tabIndex={0}
              onFocus={() => setFocused(true)}
              onBlur={() => setFocused(false)}
              onKeyDown={e => { e.preventDefault(); setKey(e.key) }}
              variant={key ? 'success' : 'info'}
            >
              {!focused && !key ? 'Click here' : !key ? 'Press a key' : JSON.stringify(key)}
            </Alert>
          )}
          {bindingMode === 'timer' && (
            <>
              <Form.Label>Timer will repeat every {timer}ms</Form.Label>
              <Form.Range
                className={'mb-0'}
                min={10} max={2000} step={10}
                value={timer}
                onChange={e => setTimer(e.target.valueAsNumber)}
              />
            </>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant={'secondary'} onClick={() => setBindingMode(null)}>Cancel</Button>
          <Button variant={'primary'} disabled={!key && bindingMode !== 'timer'} onClick={() => {
            setBindingMode(null)
            if (bindingMode === 'timer') setTriggers(setTrigger(event, { timer }))
            if (!key) return
            if (bindingMode === 'key down') setTriggers(setTrigger(event, { keyDown: key }))
            if (bindingMode === 'key up') setTriggers(setTrigger(event, { keyUp: key }))
          }}>
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
    )
  }

  return (
    <Dropdown as={ButtonGroup}>
      <Button variant={variant} onClick={onTrigger}>{event}</Button>

      <Dropdown.Toggle split variant={variant} />
      <Dropdown.Menu>
        <Dropdown.Item onClick={() => setBindingMode('key down')}>Bind to a key down...</Dropdown.Item>
        <Dropdown.Item onClick={() => setBindingMode('key up')}>Bind to a key up...</Dropdown.Item>
        <Dropdown.Item onClick={() => setBindingMode('timer')}>Bind to a timer...</Dropdown.Item>
        {triggers[event] && (
          <Dropdown.Item onClick={() => setTriggers(removeTrigger(event))}>
            Remove binding {JSON.stringify(triggers[event])}
          </Dropdown.Item>
        )}
      </Dropdown.Menu>

      <BindingModal />
    </Dropdown>
  )
}

function TriggerArea(props: { triggers: EventTriggers, onTrigger: (event: string) => void }): JSX.Element {
  const { triggers, onTrigger } = props
  const [focused, setFocused] = React.useState(false)
  const { current: timeouts } = React.useRef<{ [event: string]: NodeJS.Timeout }>({})

  React.useEffect(() => {
    function startRepeating(event: string, delay: number): void {
      timeouts[event] = setTimeout(() => {
        onTrigger(event)
        startRepeating(event, delay)
      }, delay)
    }

    for (const event in timeouts) {
      clearTimeout(timeouts[event])
      delete timeouts[event]
    }

    if (focused) {
      for (const event in triggers) {
        const trigger = triggers[event]
        if ('timer' in trigger) {
          startRepeating(event, trigger.timer)
        }
      }
    }
  }, [focused, triggers, timeouts, onTrigger])

  function onKeyDown(key: string) {
    for (const [event, trigger] of Object.entries(triggers)) {
      if ('keyDown' in trigger && trigger.keyDown === key) {
        onTrigger(event)
      }
    }
  }

  function onKeyUp(key: string) {
    for (const [event, trigger] of Object.entries(triggers)) {
      if ('keyUp' in trigger && trigger.keyUp === key) {
        onTrigger(event)
      }
    }
  }

  return (
    <Alert
      variant={focused ? 'success' : 'secondary'}
      style={{ outline: 'none' }}
      tabIndex={0}
      onFocus={() => setFocused(true)}
      onBlur={() => setFocused(false)}
      onKeyDown={e => { if (!focused) return; e.preventDefault(); onKeyDown(e.key) }}
      onKeyUp={e => { if (!focused) return; e.preventDefault(); onKeyUp(e.key) }}
    >
      {focused
        ? <>To <strong>turn off</strong> bindings and stop timers, <strong>click away</strong></>
        : <>To <strong>turn on</strong> bindings and start timers, <strong>click here</strong></>}
    </Alert>
  )
}

function Pixels(props: HTMLProps<HTMLCanvasElement> & { pixels: BigInt64Array }): JSX.Element {
  const { pixels, ...rest } = props
  return (
    <Canvas {...rest} sourceData={pixels} onDraw={(c, pxs) => {
      const ctx = c.getContext('2d')
      if (!ctx) return
      ctx.clearRect(0, 0, c.width, c.height)

      const numPixels = Math.floor(pxs.length / 5)
      if (numPixels <= 0) return

      let [minX, minY, maxX, maxY] = [+Infinity, +Infinity, -Infinity, -Infinity]
      for (let i = 0; i < numPixels; i++) {
        const [x, y] = [pxs[i*5 + 3], pxs[i*5 + 4]]
        if (x < minX) minX = Number(x)
        if (y < minY) minY = Number(y)
        if (x > maxX) maxX = Number(x)
        if (y > maxY) maxY = Number(y)
      }
      maxX++
      maxY++
      const fitSquareX = Math.min(0, ((maxX-minX) - (maxY-minY)) / 2)
      const fixSquareY = Math.min(0, ((maxY-minY) - (maxX-minX)) / 2)
      minX += fitSquareX
      minY += fixSquareY
      maxX -= fitSquareX
      maxY -= fixSquareY
      minX -= 0.2
      minY -= 0.2
      maxX += 0.2
      maxY += 0.2

      for (let i = 0; i < numPixels; i++) {
        const [r, g, b, x, y] = Array.from(pxs.slice(5*i, 5*i + 5)).map(Number)
        const x0 = (x - minX) / (maxX - minX) * c.width
        const y0 = (y - minY) / (maxY - minY) * c.height
        const w = c.width / (maxX - minX)
        const h = c.height / (maxY - minY)
        ctx.fillStyle = `rgb(${r},${g},${b})`
        ctx.fillRect(x0, y0, w, h)
      }
    }} />
  )
}

function lineAndColumn(code: string, position: number) {
  let line = 1
  let column = 1
  let i = 0
  for (const char of code) {
    if (i >= position) return { line, column }
    column += 1
    if (char === '\n') {
      line += 1
      column = 1
    }
    i += 1
  }
  return { line, column }
}

function exampleCode(): string {
  return `\
(events Left Right Up Down Tick)

(define oneNil nil)
(define oneBool true)
(define oneInt 42)
(define anotherInt 19)

(define wasItReallyNil (isnil oneNil))
(define thatWasNotNil  (not (isnil oneBool)))
(define logic
  (&& oneBool wasItReallyNil (|| false thatWasNotNil)))

(define arithmetic
  (+
    (- oneInt anotherInt)
    (* oneInt anotherInt)
    (/ oneInt anotherInt)
    (% oneInt anotherInt)))

(define comparisons
  (&&
    (== 1 1 1 1)
    (!= 3 2)
    (< 1 2 3 4 5)
    (> 5 4 3 2 1)
    (<= 1 1 2 2 3 3)
    (>= 3 3 2 2 1 1)))

(define factorial
  (lambda (n)
    (if (<= n 0)
      1
      (* n (factorial (- n 1))))))

(define factorialOf5
  (factorial 5))

(define fibonacci
  (lambda (a b n)
    (if (<= n 0)
      a
      (fibonacci b (+ a b) (- n 1)))))

(define fibonacciAt10
  (fibonacci 0 1 10))

(define doesFinacciWork
  (let ((a (fibonacci 0 1 7))
        (b (fibonacci 0 1 8))
        (c (fibonacci 0 1 9)))
    (== (+ a b) c)))

(define consThatPair
  (cons 6 7))
(define carIsFirst
  (car consThatPair))
(define cdrIsSecond
  (cdr consThatPair))

(define weMakeLists
  (cons 1 (cons 2 (cons 3 nil))))

(define tryPressingTick
  (after [(Tick; Tick; Tick)*]
    9000))

(define itIsARegularLanguage
  (after [*; Right]
    true))

(define chooseOne
  (or
    (after [*; Left]  1)
    (after [*; Right] 2)
    (after [*; Up]    3)
    (after [*; Down]  4)
    (after [*; Tick]  5)))

(define direction
  (or
    (after [*; Left; Tick*]  (cons -1 0))
    (after [*; Right; Tick*] (cons +1 0))
    (after [*; Up; Tick*]    (cons 0 -1))
    (after [*; Down; Tick*]  (cons 0 +1))
    (after [Tick*]           (cons 0 0))))

(define firstDirection
  (@ direction))

(define afterThreeEvents
  (after [^; ^; ^]
    true))

(define eventCount
  (begin [^] (count 0)
    (after [^] (+ count 1))))

(define tickCount
  (begin [^] (count 0)
    (or
      (after [Tick] (+ count 1))
      (after [^]    count))))

(define positionEveryTick
  (begin [*; Tick] (pos (cons 0 0))
    (after [*; Tick]
      (cons
        (+ (car pos) (car (@ direction)))
        (+ (cdr pos) (cdr (@ direction)))))))

(define interpolate
  (lambda (dynamic)
    (begin [^] (value (@ dynamic))
      (or
        (after [Tick] (@ dynamic))
        (after [^]    value)))))

(define position
  (interpolate positionEveryTick))

(define whatHappensHere
  (after [Tick]
    (after [Tick]
      7)))

(define joinThem
  (join [Tick] whatHappensHere))

(define previousPosition
  (interpolate
    (or
      (after [1] (@ position))
      (join [*; Tick]
        (after [(*; Tick)*]
          (let ((pos (@ position)))
            (after [*; Tick]
              pos)))))))

(define figureThisOut
  (begin [(Tick; Tick) | (Tick; Tick; Tick)] (count 0)
    (or
      (after [Tick; Tick]       (+ count 1))
      (after [Tick; Tick; Tick] (+ count 1)))))
`
}
